























import Vue from 'vue';
import SvgIcon from '@/configurator/components/utils/SvgIcon.vue';
// @ts-ignore
import SvgTurnDevice from '@/common/svgs/turn-device.svg';
// @ts-ignore
import SvgClose from '@/common/svgs/close.svg';
import {UI_STATE_MUTATIONS} from '@/configurator/store/ui-state';

export default Vue.extend({

  components: {
    SvgIcon,
  },

  computed: {
    icons(): object {
      return {
        SvgTurnDevice,
        SvgClose,
      };
    },
    showCloseButton(): boolean {
      const {isLandscape, isDesktop} = this.$store.state.uiState;
      const {isViewOnly} = this.$store.state.commonUiState;
      return (!isLandscape || isDesktop) && !isViewOnly;
    },
    showHint(): boolean {
      const {isLandscape, isDesktop, isFullscreen} = this.$store.state.uiState;
      return isLandscape && !isDesktop && !isFullscreen;
    },
  },

  methods: {
    closeFullscreen() {
      this.$store.commit(UI_STATE_MUTATIONS.SET_IS_FULLSCREEN, false);
    },
  },
});
